<template>
  <div>
    <HeaderEFTG ref="headerEFTG"></HeaderEFTG>
    <div class="container is-max-desktop">
      <div class="message">
        <div class="message-header is-size-6">BLURT Node Status</div>
        <div class="message-body">
          <div class="columns is-multiline is-size-7">
            <div
              class="column is-half-tablet is-one-third-desktop"
              v-for="(node, idx) in NodeStatus"
              :key="idx"
            >
              <div :class="'node-status notification is-' + node.css">
                <p>
                  <strong class="is-size-6">{{ node.name }}</strong>
                  <br />
                  <em
                    ><a :href="node.url" target="_blank" :title="node.name">{{
                      node.url
                    }}</a></em
                  >
                </p>
                <p>PING: {{ node.ping }} <em>ms</em></p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import HeaderEFTG from "@/components/HeaderEFTG";
import axios from "axios";
export default {
  name: "Status",
  computed: {
    NodeStatus() {
      return this.nodes["blurt"];
    },
  },
  data() {
    return {
      nodes: {
        blurt: [
          {
            css: "",
            name: "Blurt Blog by Core Team",
            status: "",
            url: "https://api.blurt.blog",
          },
          {
            css: "",
            name: "BLURT World by Core Team",
            status: "",
            url: "https://rpc.blurt.world",
          },
          {
            css: "",
            name: "Blurt Buzz by @ericet",
            status: "",
            url: "https://rpc.blurt.buzz",
          },
          {
            css: "",
            name: "Privex by @someguy123",
            status: "",
            url: "https://blurtd.privex.io",
          },
          {
            css: "",
            name: "Softmetal.xyz by @softmetal",
            status: "",
            url: "https://api.softmetal.xyz",
          },
        ],
      },
    };
  },
  components: {
    HeaderEFTG,
  },
  methods: {
    Init() {
      let nodes = this.nodes["blurt"];
      // console.log(nodes)
      for (let i = 0; i < nodes.length; i++) {
        let start = Date.now();
        axios
          .get(nodes[i].url, {
            params: {
              timestamp: start,
            },
          })
          .then((result) => {
            if (result.status === 200) {
              nodes[i].status = "OK";
              nodes[i].css = "success";
            } else {
              nodes[i].css = "warning";
            }
            let end = Date.now() - start;
            console.log(end);
            nodes[i].ping = end;
          })
          .catch((error) => {
            nodes[i].css = "danger";
            nodes[i].status = error.message;
            console.error(error);
          });
      }
    },
  },
  mounted() {
    this.Init();
  },
};
</script>

<style scoped>
.node-status.notification {
  padding: 1.25rem 1.5rem;
}
.node-status.notification p {
  overflow: hidden;
}
</style>
